import { verifyPhone } from '../../../utils/utils.js'
import UserModel from '@/models/user.js'
import uniPopup from "@/components/uni-popup/uni-popup.vue"
const userModel = new UserModel() 
export default {
	data() {
		return {
			savePhone: true,
			surplusSecond: 0, // 还剩多少秒可以重发
			readySendCode: true,  // 是否准备好发送验证码
			verifyCodeTimer: null, //定时器对象
			focusArray: ['phone', 'verify'],
			focusIndex: null,  //焦点位置
			phone: '',
			verifyCode: '',
		}
	},
	beforeDestroy() {
		clearInterval(this.verifyCodeTimer)
		this.verifyCodeTimer = null
	},
	methods:{
		savePhoneChange(){
			this.savePhone = ! this.savePhone
		},
		handleBlur(index){
			let focusArray = this.focusArray
			if(index != focusArray.length - 1 ){
				if(!this[focusArray[index + 1]]){
					this.focusIndex = index + 1
					return
				}
				this.focusIndex = null
				return
			}
			this.focusIndex = null	
		},
		getVerifyCode(){
			if(!this.readySendCode){
				return 
			}
			if(!this.phone){
				uni.showToast({
					icon:'none',
					title:'请输入电话号码'
				})
				this.focusIndex = 0
				return
			}
			
			const correctPhone = verifyPhone(this.phone)
			if(!correctPhone){
				uni.showToast({
					icon:'none',
					title:'手机号码错误，请确认'
				})
				this.focusIndex = 0
				return
			} 
			userModel.getVerifyCode(this.phone, 'A').then(res=>{
				// 请求成功,并判断code是否正确
				if(res.code === '0'){
					uni.showToast({
						title: '验证码已发送，请注意查收'
					})
					this.verifyCodeStatus = 1
					this.surplusSecond = 120
					this.focusIndex = 1
					this.verifyCodeTimer = setInterval(()=>{
						if(this.surplusSecond > 0){
							this.surplusSecond -= 1
						}else{
							this.surplusSecond = 0
							this.verifyCodeStatus = 0
							clearInterval(this.verifyCodeTimer)
						}
					}, 1000)
					this.focusIndex = 1	
				}else{
					uni.showToast({
						icon:'none',
						title:res.message
					})
				}
				
			}).catch(err=>{
				uni.showToast({
					icon:'none',
					title:err.message
				})
			})
		},
		submit(){
			if(!this.phone){
				uni.showToast({
					icon:'none',
					title:'请输入电话号码'
				})
				this.$nextTick(()=>{
					this.focusIndex = 0
				})
				return
			}
			if(!this.verifyCode){
				uni.showToast({
					icon:'none',
					title:'请输入验证码'
				})
				this.$nextTick(()=>{
					this.focusIndex = 1
				})
				return
			}
			
			// 再去验证验证码是否正确,这步省略，直接登录
			// userModel.verifyCode(this.phone, this.verifyCode).then(res=>{
			// 	// 执行真正的登录
			// 	return userModel.login(this.phone, 'A')
			// })
			userModel.login(this.phone, this.verifyCode, 'A').then(res=>{
				if(res.code === '0'){
					// 登录成功
					uni.showToast({
						title: '登录成功，即将跳转到首页'
					})
					setTimeout(()=>{
						uni.reLaunch({
							url: '../../customer/main/main'
						})
					},2000)	
				}else{
					uni.showToast({
						icon: 'none',
						title: res.message
					})
				}
				
			})
			.catch(err=>{
				uni.showToast({
					icon: 'none',
					title: err.msg
				})
			})
		}
	}
}
